.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-color: blueviolet; */
    text-align: center;
    min-height: 100vh;
    /* background-image: url('./wp2856135.gif'); */
    background-image: url(https://i.pinimg.com/originals/b7/27/c7/b727c71746c3a08bf3527c884947bef9.jpg);
    
    /* background-color: #181818; */
    
}

.text {
    width: 40vw;
    color: white;
    font-family: 'Raleway', sans-serif;
    font-size: 9vh;
    font-weight: lighter;
    display: inline-block;
    letter-spacing: 1px;
    transition-duration: 0.5s;
    
}
/* .text:hover {
    padding-bottom: 18px;
    transition-duration: 0.5s;
} */



.juan {
    color: white;
    font-family: 'Raleway', sans-serif;
  
    font-weight: bold;
    color: rgba(255, 0, 157, 0.753);    
}

.button {
    width: 20%;
    border-width: 1px;
    margin-top: 2vh;
    border-style: solid;
    border-width: 0.5px;
    border-color: white;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    
}

.button-text {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    font-family: 'Raleway', sans-serif;
    font-size: 17px;
    color: white;   
    margin-top: 2%; 
    border-style: solid;
    border-width: 0.5px;
    padding: 1.2%;
    transition: 0.3s ease;
    border-radius: 5px;
}

.button-text:hover {
    color: rgba(255, 0, 157, 0.753);
    cursor: pointer;    
}

.arrow {
    transition: 0.2s ease;
}

.arrow:hover {
    transform: rotate(90deg);
}

.particles {
    position: absolute;
    top: 0px;
}


.particles4 {
    position: absolute;
    bottom: 0px;
}

.title {
    display: flex;
    align-items: center;
    flex-direction: column;
    /* border-style: solid; */
}

.title-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    /* background-color: white; */    
    position: relative;
}


.mid {
    position: absolute;
    left: 5%;
    /* margin-left: 40px; */
    /* border-style: solid; */

}

.mid2 {
    position: absolute;
    right: 5%;
    /* border-style: solid; */
}

.link1 {
    display: flex;
    align-items: center;
    justify-content: center;
    
}

@media (max-width: 1920px) {
    .text {
        font-size: 4.5vh;
    }
    .button-text {
        font-size: 20px;
    }    
}


@media (min-width: 1440px) and (max-width: 1919px) {
    .text {
        font-size: 4vh;
        width: 60vw;
    }
    .button-text {
        font-size: 17px;
    }
}


@media (min-width: 1280px) and (max-width: 1439px) {
    .text {
        font-size: 4vh;
        width: 70vw;
    }
    .button-text {
        font-size: 16px;
    }
}

@media (min-width: 768px) and (max-width: 1279px) {
    .text {
        font-size: 3vh;
        width: 90vw;
    }
    .button-text {
        font-size: 17px;
    }    
    .mid {
        display: none;
    }
    .mid2 {
        display: none;
    }
}

@media (min-width: 320px) and (max-width: 767px) {
    .text {
        font-size: 20px;
        width: 90vw;
    }
    .button-text {
        font-size: 13px;
    }
    .mid {
        display: none;
    }
    .mid2 {
        display: none;
    }
}
