.skill-section {
    background-color: #303030;
    border-style: solid;
    border-color: #383838;
}

.skills-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 5%;
    padding-right: 5%;
    align-items: center;
    margin-top: 10vh;
    height: 65vh;
    width: 100vw;
    /* border-style: solid; */
    /* border-width: 1px; */
}

.skills-box {
    display: flex;
    flex-direction: column;    
    align-items: center;
    position: relative;
    height: 100%;
    padding-top: 1.5%;
    font-size: 14px;
    font-family: Ubuntu;    
}

.skill {
    /* z-index: 2; */
    position: relative;
    display: flex;
    flex-direction: row;
    /* border-style: solid;
    border-width: 1px;    
    border-color: black; */
    justify-content: space-between;
    align-items: center;
    width: 60vh;
    background-color: #282828;
    height: 3.3vh;
    /* border-radius: 10px; */
    margin-bottom: 3%;
    color: black;
}

.box1 {
    position: absolute;
    /* border-style: solid; */
    border-width: 1px;
    border-color: black;
    /* padding: 0.5%; */
    width: 4.5vw;
    background-color: crimson;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border-radius: 10px; */
    color: white;    
}

.box2 {
    background-color: bisque;
    width: 5px;
}

.text-about {
    text-align: left;
    width: 45vw;
    font-family: 'Raleway', sans-serif;
    font-size: 22px;
    letter-spacing: 0.9px;
    line-height: 4vh;    
    font-weight: lighter;
    vertical-align: text-top;
    color: white;
}

.about {
    display: flex;    
    height: 100%;
    /* vertical-align: text-top;     */
    /* border-style: solid; */
    border-width: 1px;
    width: 100%;
}

/* @keyframes slideInFromLeft {
    0% { width: 0; }
    100% { width: 50%; }
} */



.box3 {    
    background-color: rgba(100, 11, 88, 0.924);
    width: 50%;
    animation: 2s ease-out 0s 1 slideInFromLeft;
    height: 100%;
    /* border-radius: 10px; */    
}

.skills-box2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;    
    height: 100%;
    width: 100%;
    /* bottom: auto; */
    /* border-style: solid; */
    font-size: 14px;
    color: white;
    border-width: 1px;
    padding-bottom: 6%;
    padding-right: 5%;

}

.left {
    /* animation: 2s ease-out 0s 1 slideInFromLeft; */
    /* padding-right: 30%; */
    /* padding-bottom: 1%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
    /* border-style: solid;
    border-width: 1px; */
    /* padding-bottom: 10%; */
}

.right {
    /* animation: 2s ease-out 0s 1 slideInFromRight; */
    /* padding-left: 30%; */
    /* padding-bottom: 1%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    /* border-style: solid; */
    /* border-width: 1px; */
    height: 100%;
    justify-content: space-evenly;
    padding-bottom: 12%;
}

.js {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100px;
    max-width: 100px;
    /* margin-bottom: -15px; */
}
.js-logo {
    max-height: 80%;
}

.html-logo {
    max-height: 50%;
}

.html {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 60px;
    max-width: 60px;
}



.sequelize {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100px;
    max-width: 100px;
}

.sequelize-logo {
    max-height: 90%;
}


.redux {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100px;
    max-width: 100px;
}

.redux-logo {
    max-height: 80%;
}

.postgres {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 130px;
    max-width: 130px;
}

.postgres-logo {
    max-height: 95%;
}

.graph {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100px;
    max-width: 100px;
}

.graph-logo {
    max-height:85%;
    max-width: 85%;
}



.react {
    display: flex;
    flex-direction: column;
    max-height: 140px;
    max-width: 140px;
}

.App-logo {
    /* height: 5vmin; */
    pointer-events: none;
    max-height: 100%;
  }

.css {
    display: flex;
    flex-direction: column;
    max-height: 115px;
    max-width: 105px;
}

.css-logo {
    max-height: 95%;
}

.express {
    display: flex;
    flex-direction: column;
    max-height: 100px;
    max-width: 100px;
}

.express-logo {
    max-height: 80%;
}

.nodeJs {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-height: 130px;
    max-width: 130px;
}

.nodeJs-logo {
    max-height: 80%;
    max-width: 70%;

}

.s-contact {
    display: inline-block;
    border-style: solid;
    border-width: 1px;
    padding: 1%;
    border-radius: 5px;
    transition: 0.3s ease;
}

.s-contact:hover {
    color: rgba(255, 0, 157, 0.753);
    cursor: pointer;    
}


  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 5s linear;
    }
  }

  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

