@media (min-width: 768px) and (max-width: 1279px) {
    .skill-section{
        width: 100vw;
    }
    .text-about {
        text-align: center;
        width: 45vw;
        font-size: 2.3vw;
    }
    .right {
        padding-bottom: 30%;
    }
}

@media (min-width: 414px) and (max-width: 767px) {
    .skill-section{
        /* width: 100vw; */
        height: 70vh;
    }
    .text-about {
        text-align: center;
        width: 45vw;
        font-size: 2.8vw;
    }
    .right {
        padding-bottom: 30%;
    }
    /* .skills-wrapper {
        flex-direction: column;
    } */
    .skills-box2 {
        transform: scale(0.6);
        height: 100%;
        width: 100%;
        padding-bottom: 60%;

    }
}
@media (min-width: 320px) and (max-width: 413px) {
    .skill-section{
        /* width: 100vw; */
        height: 70vh;
  
        display: flex;
        align-items: center;
        align-content: center;
    }
    .text-about {
        align-content: center;
        text-align: center;
        width: 60vw;
        font-size: 3vw;
    }
    .right {
        padding-bottom: 30%;
    }
    /* .skills-wrapper {
        flex-direction: column;
    } */
    .skills-box2 {
       display: none;

    }
    .skills-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 0%;
        padding-right: 0%;
        align-items: center;
        margin-top: 10vh;
        height: 55vh;
        width: 100vw;
        text-align: center;
        
    }
    .about {
        display: flex;  
        /* text-align: center; */
        justify-content: center;
        /* align-items: center; */
        /* align-content: center;   */
        height: 100%;
        /* vertical-align: text-top;     */
        /* border-style: solid; */
        border-width: 1px;
        width: 100%;
    }

}